import React, {useEffect, useState} from 'react';
import { CButton, CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from '@coreui/react';

import './clinic.scss';
import { Link } from 'react-router-dom';


const ClinicList=()=> {
  const [doctor, setDoctor] = useState([]);
  const [auth, seAuth] = useState([]);
  useEffect(() => {
    fetch(process.env.REACT_APP_API_URL+"clinic/list", {
        method: 'POST', // or 'PUT'
        headers: {
            'Content-Type': 'application/json',
        }
    }).then(e=>e.json()).then((data)=>{
        console.log(data);
        setDoctor(data.data)
    })
    seAuth(JSON.parse(localStorage.getItem("user")))
  }, []);
  

  return (<>
    <div className='d-flex justify-content-between align-items-center mb-3'>
        <h2 className='mb-3'>Clinic List</h2>
        {auth.role==="ADMIN" &&<Link className='btn btn-lg btn-primary' to={"/clinic/add"}>Add Clinic</Link>}
    </div>
    <div className='arTableWrapper'>
       <CTable>
            <CTableHead>
                <CTableRow>
                <CTableHeaderCell width={100} scope="col">UID</CTableHeaderCell>
                <CTableHeaderCell scope="col">Clinic Name</CTableHeaderCell>
                <CTableHeaderCell scope="col">Clinic Mobile No</CTableHeaderCell>
                <CTableHeaderCell scope="col">Clinic Email</CTableHeaderCell>
                <CTableHeaderCell scope="col">Owner Name</CTableHeaderCell>
                <CTableHeaderCell scope="col">Location</CTableHeaderCell>
                <CTableHeaderCell scope="col">Registration No</CTableHeaderCell>
                </CTableRow>
            </CTableHead>
            <CTableBody>
                {doctor.map((i, k)=>(
                    <CTableRow key={k}>
                        <CTableDataCell scope="row"><Link to={'/clinic/'+i._id}>{i.uid}</Link></CTableDataCell>
                        <CTableDataCell>{i.clinic_name}</CTableDataCell>
                        <CTableDataCell>{i.clinic_phone}</CTableDataCell>
                        <CTableDataCell>{i.clinic_email}</CTableDataCell>
                        <CTableDataCell>{i.first_name} {i.last_name}</CTableDataCell>
                        <CTableDataCell>{i.lat}<sup>°</sup> N, {i.long}<sup>°</sup> E</CTableDataCell>
                        <CTableDataCell>{i.registration_no}</CTableDataCell>                    
                    </CTableRow>
                ))}                
            </CTableBody>
        </CTable>
    </div>
    </>
  )
}
export default ClinicList;